.lazyload {
  position: relative;
  padding-top: 75%;
}

.lazyload__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.lazyload__image--lazy {
  opacity: 0;
}

.lazyload--image-loaded {
  display: block;
}

.lazyload--image-loaded .lazyload__image--loaded {
  opacity: 0;
  animation-name: fadein;
  animation-duration: 1s;
  animation-delay: 700ms;
  animation-fill-mode: forwards;
}

.lazyload--image-loaded::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fbd664;
  transform: scaleX(0);
  transform-origin: 0 50%;
  animation-name: swiper;
  animation-iteration-count: 1;
  animation-duration: 700ms;
  animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  z-index: 20;
}

