pre[class*='language-'],
:not(pre) > code[class*='language-'] {
  background: #2a2a2a;
}

pre[class*='language-'] {
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #e1e1e8;
}

:not(pre) > code[class*='language-'] {
  padding: 0.15em 0.2em 0.05em;
