}

.token.comment, .token.prolog, .token.doctype, .token.cdata {
  color: #6f705e;
}

.token.null, .token.operator, .token.boolean, .token.number {
  color: #a77afe;
}

.token.attr-name, .token.string {
  color: #e6d06c;
}

.token.entity, .token.url,
.language-css .token.string,
.style .token.string {
  color: #e6d06c;
}

.token.selector {
  color: #a6e22d;
}

.token.atrule, .token.attr-value, .token.keyword, .token.control, .token.directive, .token.important, .token.unit {
  color: #ef3b7d;
}

.token.regex, .token.statement {
  color: #76d9e6;
}

.token.placeholder, .token.variable {
  color: #ffffff;
}

.token.important, .token.statement {
  font-weight: 700;
}

.token.punctuation {
  color: #bebec5;
}

.token.entity {
  cursor: help;
}

.token.debug {
  color: #df0000;
}

pre[data-line] {
  position: relative;
  padding: 1em 0 1em 3em;
}

.line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  padding: inherit 0;
  margin-top: 1em;
  background: rgba(255, 255, 255, 0.08);
  pointer-events: none;
  line-height: inherit;
  white-space: pre;
}

.line-highlight:before,
.line-highlight[data-end]:after {
  content: attr(data-start);
  position: absolute;
  top: 0.4em;
  left: 0.6em;
  min-width: 1em;
  padding: 0.2em 0.5em;
  background-color: rgba(255, 255, 255, 0.4);
  color: #000000;
